@import "_reset";
@import "_variables";
@import "vendor/_sprite-mixins.scss";


body{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: $red;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
}

img{
    width: 100px;
}

h1{
    margin-top: 10px;
    color:white;
    font-size:40px;
    font-weight: 200;
}

footer{
    display: none;
}

.ex_use_sprite {
    @include use-sprite('btn-more');
}